<template>
	<div class="chartWrap">
		<div class="btn-div" v-if="showConfig">
			<el-button type="primary" @click="paramConfig"></el-button>
		</div>
		<div :id="chartId" class="chart-div"></div>
	</div>
</template>
<script>
export default {
  name: "AChart",
  props: {
    chartData: {
      type: Object,
      required: true,
      default() { // 
        return {};
      }
    },
	  showConfig: {
      type: Boolean,
		  default: true
    }
  },
  data() {
    return {
      myChartData: this.chartData
    };
  },
  methods: {
    renderChart() { // 
      this.myChart.clear();
      let _options = Object.assign(
          {
            color: this.colorArr
          },
          this.myChartData
      );
      this.myChart.setOption(_options);
    },
    paramConfig() {
      this.$emit('paramConfig')
    }
  },
  computed: {
    chartId() { // ID
      return Math.random()
          .toString(36)
          .substr(2);
    },
    myChart() { // ID
      return this.$echarts.init(document.getElementById(this.chartId));
    },
    colorArr() { // 
      return this.$store.state.chartTheme;
    }
  },
  mounted() {
    window.onresize = () => { // echartsAPI
      this.myChart.resize();
    };
  },
  beforeDestroy() {
    window.onresize = null; // 
  },
  watch: { // deep
    chartData: {
      handler(v, o) {
        this.myChartData = v;
        this.renderChart();
      },
      deep: true
    },
    colorArr(n, o) {
      this.renderChart();
    }
  }
};
</script>
<style lang="scss">
	#myChartWrap {
		width: 100%;
		height: 55vh;
	}
	.chart-div {
		height: 360px;
	}
	.btn-div {
		text-align: right
	}
</style>
